import { NgModule } from '@angular/core';
import { CommonModule }      from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { TypeOfHousingComponent } from './type-of-housing.component';
import { TypeOfHousingRoutingModule } from './type-of-housing-routing.module';
import { DialogModule } from 'primeng/primeng';
import {PaginatorModule} from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {AutoCompleteModule} from 'primeng/primeng';
import {CalendarModule} from 'primeng/primeng';
import {ButtonModule} from 'primeng/primeng';
import {DropdownModule} from 'primeng/primeng';
import { LightboxModule } from 'angular2-lightbox';
@NgModule({
  imports: [
    PaginatorModule,
    CommonModule, 
    FormsModule,
    HttpModule,
    TypeOfHousingRoutingModule,
    DialogModule,
    BusyModule,
    AutoCompleteModule,
    CalendarModule,
    ButtonModule,
    DropdownModule,
	LightboxModule
  ],
  declarations: [
    TypeOfHousingComponent
  ]
})
export class TypeOfHousingModule { }



